html {
  font-size: 10px;
}

* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

body {
  font-family: Impact, Charcoal, sans-serif;
  margin: 0;
  padding: 0;
  background-color: rgb(244, 244, 244);
  color: rgb(13, 37, 63);
}

p {
  font-size: 1.6rem;
}

span {
  font-size: 1.8rem;
  color: rgb(1, 180, 228);
}

.container {
  margin: 0 auto;
  max-width:1000px;
  padding: 15px;
}

.title {
  font-size: 4.4rem;
  text-align: center;
}

.form {
  display:grid;
}

.label {
  font-size: 1.8rem;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 1rem;
}

.input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 2.8rem;
  border-radius: 20px;
  border: 1px solid #ddd;
  margin-bottom: 1rem;
}

.button {
  font-family: Impact, Charcoal, sans-serif;
  background-color: rgb(1, 180, 228);
  color: rgb(13, 37, 63);
  padding: 1rem 2rem;
  border: 2px solid rgb(13, 37, 63);
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color, 500ms;
  transition: color, 500ms;
  transition: border, 500ms;
}

.button:hover {
  background-color: rgb(144, 206, 161);
  color: white;
  border: 2px solid white;
}

.card-list {
  margin-top: 4rem;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 2rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
  margin-bottom: 2rem;
  background-color: white;
}

.card-top {
  display: block;
}

.card--title {
  margin: 0;
  font-size: 3.2rem;
  color: rgb(1, 180, 228);
}

.card--image {
  margin-right: 2rem;
  max-width: 185px;
  max-height: 278px;
}

.credits {
  width: 239px;
  height: 30px;
  margin: 0 auto;
  text-align: center;
}

h6 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

@media (min-width:769px){


  .form {
      grid-template-columns: auto 1fr auto;
      grid-gap: 1rem;
      align-items: center;
  }

  .label {
    margin-bottom: 0.2rem;
  }
  
  .input {
      margin-bottom: 0;
  }

  .card {
    display: flex;
    flex-direction: row;
    padding: 4rem 4rem;
  }
}

@media (min-width:481px){
  .container {
    padding: 40px;
  }
}